@import './variables/colors.scss';


.leaflet-container {    
    height: 100vh;
    left: 0;
    position: absolute!important;
    right: 0;
    top: 0%;
    -webkit-transition: all .5s ease-in-out;
    -moz-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;    
    transform: translate(0, 0);
    width: 100%;
    &.show-content{
        transform: translate(00%, 0);
        width: 60%;
    }
    .leaflet-control{
        button{
            background: #96281b;
            border: 1px solid #96281b;
            padding: 10px 20px;
            color: #FFF;
            font-size: 15px;            
        }
        input[type=checkbox]{
            height: 0;
            width: 0;
            visibility: hidden;
        }
        
        label {
            cursor: pointer;
            text-indent: -9999px;
            width: 60px;
            height: 30px;
            background: grey;
            display: block;
            border-radius: 15px;
            position: relative;
        }
        
        label:after {
            content: '';
            position: absolute;
            top: 3px;
            left: 3px;
            width: 23px;
            height: 23px;
            background: #fff;
            border-radius: 30px;
            transition: 0.3s;
        }
        
        input:checked + label {
            background: #96281b;
        }
        
        input:checked + label:after {
            left: calc(100% - 5px);
            transform: translateX(-100%);
        }
        
        label:active:after {
            width: 23px;
        }

    }
}


.list-popup{
    list-style: none;
    padding-left: 0;
    li{
        display: inline-block;
        text-align: center;
        width: 100%;
        &.title{
            font-size: 18px;
            font-weight: bold;
        }
    }
}

.img-wiki{
    position: absolute;
    z-index: 1000;
    width: 110px;
    bottom: 30px;
    left: 30px;    
}



